'use client';

import React, { useRef, useState, useEffect } from 'react';
import { useFrame, useThree } from '@react-three/fiber';
import * as THREE from 'three';

const AXIS_LENGTH = 3;
const ARROW_HEAD_LENGTH = 0.6;
const ARROW_HEAD_RADIUS = 0.15;
const LINE_WIDTH = 0.1;
const HITBOX_SIZE = 1;

function Arrow({ direction, color, onDragStart, onDrag, onDragEnd }) {
  const arrowGroupRef = useRef();
  const hitboxRef = useRef();
  const [isDragging, setIsDragging] = useState(false);
  const [hovered, setHovered] = useState(false);
  const { camera, size, viewport } = useThree();
  const startMousePos = useRef(new THREE.Vector2());

  const axis = new THREE.Vector3(...direction).normalize();
  const arrowEnd = axis.clone().multiplyScalar(AXIS_LENGTH);

  const getWorldDelta = (deltaX, deltaY) => {
    if (camera.isOrthographicCamera) {
      const worldDelta = new THREE.Vector3();
      const zoom = camera.zoom;
      worldDelta.x = (deltaX / size.width) * (camera.right - camera.left) / zoom;
      worldDelta.y = -(deltaY / size.height) * (camera.top - camera.bottom) / zoom;
      worldDelta.z = 0;
      return worldDelta;
    } else {
      const mouse = new THREE.Vector2();
      mouse.x = (deltaX / size.width) * 2;
      mouse.y = -(deltaY / size.height) * 2;
      const raycaster = new THREE.Raycaster();
      raycaster.setFromCamera(mouse, camera);
      return raycaster.ray.direction.multiplyScalar(10);
    }
  };

  const handlePointerDown = (e) => {
    e.stopPropagation();
    setIsDragging(true);
    startMousePos.current.set(e.clientX, e.clientY);
    
    if (onDragStart) onDragStart(direction);
  };

  const handlePointerMove = (e) => {
    if (!isDragging) return;
    e.stopPropagation();
    
    const deltaX = e.clientX - startMousePos.current.x;
    const deltaY = e.clientY - startMousePos.current.y;
    
    const worldDelta = getWorldDelta(deltaX, deltaY);
    const axisMovement = worldDelta.dot(axis);
    
    if (onDrag && Math.abs(axisMovement) > 0.001) {
      const movement = new THREE.Vector2(
        direction[0] !== 0 ? axisMovement : 0,
        direction[1] !== 0 ? axisMovement : (direction[2] !== 0 ? -axisMovement : 0)
      );
      onDrag(direction, movement);
    }
    
    startMousePos.current.set(e.clientX, e.clientY);
  };

  const handlePointerUp = () => {
    if (isDragging) {
      setIsDragging(false);
      if (onDragEnd) onDragEnd();
    }
  };

  useEffect(() => {
    if (isDragging) {
      window.addEventListener('pointermove', handlePointerMove);
      window.addEventListener('pointerup', handlePointerUp);
      return () => {
        window.removeEventListener('pointermove', handlePointerMove);
        window.removeEventListener('pointerup', handlePointerUp);
      };
    }
  }, [isDragging]);

  const getRotation = () => {
    if (direction[0] !== 0) {
      return [0, 0, -Math.PI / 2];
    } else if (direction[1] !== 0) {
      return [0, 0, 0];
    } else {
      return [Math.PI / 2, 0, 0];
    }
  };

  return (
    <group 
      ref={arrowGroupRef}
      onClick={(e) => e.stopPropagation()}
      onPointerDown={(e) => e.stopPropagation()}
    >
      <mesh
        ref={hitboxRef}
        position={[arrowEnd.x * 0.5, arrowEnd.y * 0.5, arrowEnd.z * 0.5]}
        onPointerDown={handlePointerDown}
        onPointerOver={() => setHovered(true)}
        onPointerOut={() => setHovered(false)}
        visible={false}
      >
        <boxGeometry args={[HITBOX_SIZE, HITBOX_SIZE, AXIS_LENGTH]} />
      </mesh>
      
      <group rotation={getRotation()}>
        <mesh position={[0, 0, (AXIS_LENGTH - ARROW_HEAD_LENGTH) / 2]}>
          <cylinderGeometry args={[LINE_WIDTH / 2, LINE_WIDTH / 2, AXIS_LENGTH - ARROW_HEAD_LENGTH, 8]} />
          <meshBasicMaterial color={color} transparent opacity={hovered || isDragging ? 1 : 0.7} />
        </mesh>
        
        <mesh position={[0, 0, AXIS_LENGTH - ARROW_HEAD_LENGTH / 2]}>
          <coneGeometry args={[ARROW_HEAD_RADIUS, ARROW_HEAD_LENGTH, 8]} />
          <meshBasicMaterial color={color} transparent opacity={hovered || isDragging ? 1 : 0.7} />
        </mesh>
      </group>
    </group>
  );
}

export default function TransformGizmo({ 
  position = [0, 0, 0], 
  onDragStart, 
  onDrag, 
  onDragEnd,
  visible = true 
}) {
  const gizmoRef = useRef();
  const { camera } = useThree();
  
  useEffect(() => {
    console.log('TransformGizmo mounted/updated', { position, visible });
  }, [position, visible]);
  
  useFrame(() => {
    if (!gizmoRef.current || !visible) return;
    
    const cameraDirection = new THREE.Vector3();
    camera.getWorldDirection(cameraDirection);
    gizmoRef.current.lookAt(
      gizmoRef.current.position.clone().add(cameraDirection)
    );
  });

  if (!visible) return null;

  return (
    <group 
      ref={gizmoRef} 
      position={position}
      onClick={(e) => e.stopPropagation()}
      onPointerDown={(e) => e.stopPropagation()}
    >
      <Arrow
        direction={[1, 0, 0]}
        color={0x0000ff}
        onDragStart={onDragStart}
        onDrag={onDrag}
        onDragEnd={onDragEnd}
      />
      <Arrow
        direction={[0, 1, 0]}
        color={0x00ff00}
        onDragStart={onDragStart}
        onDrag={onDrag}
        onDragEnd={onDragEnd}
      />
      <Arrow
        direction={[0, 0, 1]}
        color={0xff0000}
        onDragStart={onDragStart}
        onDrag={onDrag}
        onDragEnd={onDragEnd}
      />
      
      <mesh position={[0, 0, 0]}>
        <sphereGeometry args={[0.1, 8, 8]} />
        <meshBasicMaterial color={0xffffff} />
      </mesh>
    </group>
  );
}
